class homepageHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    
    };
  }



  pushNext() {
    this.props.history.push("/");
  }


 
  render() {
    return (
      <div className='headerMain'>

      <div className='headerMenu'>

      <NavLink to="/">  <img  className='logoSize'  src={Logo} /> </NavLink>
    
      <h4 style={{margin:'1px'}}>ka-fal Music </h4>
      </div>
     
      </div>
    );
  }
}

export default homepageHeader;
