import React, { Component } from "react";
import "./Homepage.css";
import HomeHeader from "../../Reusable/Header/homeHeader";
import MainFooterPage from "../../Reusable/Footer/footer";
import artist from "../../Assets/images/artist.jpg";


export default class Homepage extends Component {
  state = {
   
    loading: true,
   
  };
  pushNext() {
    this.props.history.push("/2");
  }

  render() {
    
  
      return (
        <div  id='homepage' >
          <HomeHeader
          
          />
          <div  className="container">

          <div className="homepageContent">

          <div className="artistDiv" onClick={() => this.pushNext()}>
          <img    src={artist} />
          <h3>Artist</h3>
          </div>
         
   
          </div>

         
        </div>
          
        <div className="container1">
        <div className="homepageContent">
          <h1>This is Ganesh Music</h1>
          </div>
        </div>

        <div className="container2">
        <div className="homepageContent">
        <h1>kulli again Music</h1>
        </div>
        </div>
         
              <MainFooterPage  />
           
        </div>
      );
    
  }
}
